import React, { useState } from "react";
import Axios from "axios";
import { Form, Input, Row, Col, Tooltip, Select, Button } from "antd";
import { QuestionCircleOutlined } from "@ant-design/icons";
import "./ContactPage.css";

const { TextArea } = Input;

function ContactPage() {
  const [NameValue, setNameValue] = useState("");
  const [EmailValue, setEmailValue] = useState("");
  const [PhoneValue, setPhoneValue] = useState("");
  const [MessageValue, setMessageValue] = useState("");
  const [form] = Form.useForm();

  const onNameChange = (event) => {
    setNameValue(event.currentTarget.value);
  };
  const onEmailChange = (event) => {
    setEmailValue(event.currentTarget.value);
  };
  const onPhoneChange = (event) => {
    setPhoneValue(event.currentTarget.value);
  };
  const onMessageChange = (event) => {
    setMessageValue(event.currentTarget.value);
  };

  const onSubmit = () => {
    if (!NameValue || !EmailValue || !PhoneValue || !MessageValue) {
      return alert("Fill all Fields");
    }

    const variables = {
      name: NameValue,
      email: EmailValue,
      phone: PhoneValue,
      message: MessageValue,
    };

    Axios.post("/api/message/uploadMessage", variables).then((response) => {
      if (response.data.success) {
        alert("message Sent");
      } else {
        alert("failed to Send message");
      }
    });

    form.resetFields();
  };

  return (
    <div className="ContactPage">
      <Col
        xs={24}
        sm={20}
        md={22}
        lg={18}
        style={{
          margin: "auto",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <Row
          gutter={16}
          justify="center"
          align="middle"
          style={{
            background: "rgba(0,0,0,0.5)",
            padding: "1rem",
            maxWidth: "1000px",
          }}
        >
          <Col xs={24} md={12} className="contactInfo">
            <h1>
              Holiday <span className="span">Hikers</span>
            </h1>

            <p>info@holidayhikers.com</p>

            <p>
              Street: 17/F BPI
              <br /> Bldg. City: Ayala Ave. Cor Paseo De Roxas
              <br />
              State/province/area: Ayala Ave. cor Paseo
              <br /> Country Philippines{" "}
            </p>

            <p>
              <br />
              (02) 845-5045
            </p>

            <p> +63(915)7880468</p>

            <p>9am to 6pm Monday to Friday</p>
          </Col>
          <Col xs={24} md={12} className="contactForm">
            <Form
              form={form}
              onFinish={onSubmit}
              // onSubmit={onSubmit}
            >
              <Row gutter={16}>
                <h1>
                  Contact <span className="span">Us</span>
                </h1>
                <Col span={24}>
                  <Form.Item
                    name="name"
                    rules={[
                      {
                        required: true,
                        message: "Please input your name!",
                        whitespace: true,
                      },
                    ]}
                  >
                    <Input
                      size="large"
                      onChange={onNameChange}
                      value={NameValue}
                      placeholder="Full Name"
                    />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    name="email"
                    rules={[
                      {
                        type: "email",
                        message: "The input is not valid E-mail!",
                      },
                      {
                        required: true,
                        message: "Please input your E-mail!",
                      },
                    ]}
                  >
                    <Input
                      size="large"
                      onChange={onEmailChange}
                      value={EmailValue}
                      placeholder="Email Address"
                    />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  {" "}
                  <Form.Item
                    name="phone"
                    rules={[
                      {
                        required: true,
                        message: "Please input your phone number!",
                      },
                      {
                        min: 11,
                        message: "Must be minimum of 11 numbers.",
                      },
                      {
                        max: 11,
                        message: "Must be maximum of 11 numbers.",
                      },
                    ]}
                  >
                    <Input
                      size="large"
                      onChange={onPhoneChange}
                      value={PhoneValue}
                      placeholder="Phone Number"
                    />
                  </Form.Item>
                </Col>

                <Col span={24}>
                  {" "}
                  <Form.Item
                    name="message"
                    rules={[
                      {
                        required: true,
                        message: "Please input your message!",
                      },
                      {
                        min: 20,
                        message: "message must be minimum of 20 characters.",
                      },
                      {
                        max: 200,
                        message: "message must be maximum of 200 characters.",
                      },
                    ]}
                  >
                    <TextArea
                      onChange={onMessageChange}
                      value={MessageValue}
                      placeholder="Message"
                    />
                  </Form.Item>
                </Col>

                <Button
                  // onClick={onSubmit}
                  type="primary"
                  htmlType="submit"
                  size="large"
                  style={{ margin: "auto", width: "200px" }}
                >
                  Submit
                </Button>
              </Row>
            </Form>
          </Col>
        </Row>
      </Col>
    </div>
  );
}

export default ContactPage;
